import listCell from '../../components/mix-list-cell';

// const plug = uni.requireNativePlugin('Html5app-Baichuan');
export default {
	components: {
		listCell
	},
	data() {
		return {
			couponlist: [],
			des: '会员申请',
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			mobile: '',
			relation_id: '',
			jifen: '0',
			image_url: '/static/logo.png',
			gender: '',
			moneyAll: '0',
			moneys: '0',
			jiesuan: '0',
			grade: '',
			phone: '',
			dengji: 0,
			dengjides: '',
			lastjiesuan: '0',
			lastMoneyAll: '0',
			orderNum: '0',
			orderMonthNum: '0',
			totalMoney: '0',
			isInvitation: 0,
			moving: false,
			isEnable: "否",
			footprintKey: 'orange-sqx-footprint',
		}
	},

	onLoad() {
		let a = this.$queue.getData("isEnable")
		if (a) {
			this.isEnable = a;
		}
		let userId = this.$queue.getData("userId");
		if (userId) {
			this.getUserInfo(userId)
		}










	},
	onShow() {
		let mobile = this.$queue.getData("nickName");
		this.phone = this.$queue.getData("mobile");
		let image_url = this.$queue.getData("image_url");
		let gender = this.$queue.getData("gender");

		if (mobile && mobile !== 'undefined') {
			this.mobile = mobile;
		} else {
			this.mobile = '';
		}
		if (image_url && image_url !== 'undefined') {
			this.image_url = image_url;
		} else {
			this.image_url = '/static/img/common/logo.jpg'
		}
		if (gender) {
			this.gender = gender;
		}
		let relation_id = this.$queue.getData("relation_id");
		if (relation_id && relation_id !== 'undefined') {
			this.des = '我的特权';
			this.relation_id = relation_id;
		} else {
			this.des = '会员申请';
			this.relation_id = '';
		}
		let userId = this.$queue.getData("userId");
		if (userId) {
			this.getMoney();
			this.getUserInfo(userId);
			this.couponlist = this.$queue.get(this.footprintKey);
			this.$Request.getT("/cash/money/" + userId).then(res => {
				if (res.status === 0 && res.data) {
					this.moneys = parseFloat(res.data).toFixed(0);
				} else {
					this.moneys = '0';
				}
			});
		} else {
			this.totalMoney = '0';
			this.orderNum = '0';
			this.moneys = '0';
			this.moneyAll = '0';
			this.lastjiesuan = '0';
			this.lastMoneyAll = '0';
			this.orderMonthNum = '0';
			this.jiesuan = '0';
			this.jifen = '0'
		}

	},

	methods: {
		//升级规则弹框
		shengJiMethod() {
			uni.showModal({
				title: '返费提升规则',
				content: '每成功邀请2位好友提升1级\n最高10级每级对应相应的返现比例\n\n邀请方式：\n1、点击右下方生成海报按钮\n2、将海报分享好友、群或朋友圈\n让好友扫码关注即可升级',
				showCancel: true,
				cancelText: '关闭',
				confirmText: '生成海报',
				success: res => {
					if (res.confirm) {
						uni.navigateTo({
							url: '/pages/share/shares'
						})
					}
				}
			});
		},
		//邀请码复制
		copyHref() {
			uni.setClipboardData({
				data: this.relation_id,
				success: (r => {
					this.$queue.showToast("邀请码复制成功")
				}),
			})
		},
		//获取用户信息
		getUserInfo() {
			this.$Request.getT(this.$Api.member.GetUserInfoById).then(res => {
				if (res.Code === 1) {
					if (res.Message.Point) {
						this.jifen = parseFloat(res.Message.Point).toFixed(0);
					}
					res.Message.HeadImgUrl = res.Message.HeadImgUrl ? this.$Url + res.Message.HeadImgUrl : '/static/img/common/logo.jpg';
					this.$queue.setData("image_url",  res.Message.HeadImgUrl);
					this.$queue.setData("relation_id", res.Message.RegionId);
					this.$queue.setData("nickName", res.Message.NickName);
					this.$queue.setData("isInvitation", res.Message.isInvitation);
					let grade = res.Message.UserGroupId;
					if (grade == 0.3) {
						this.dengji = 0;
						this.dengjides = "还可升级10级";
					} else if (grade == 0.34) {
						this.dengji = 1;
						this.dengjides = "还可升级9级";
					} else if (grade == 0.38) {
						this.dengji = 2;
						this.dengjides = "还可升级8级";
					} else if (grade == 0.42) {
						this.dengji = 3;
						this.dengjides = "还可升级7级";
					} else if (grade == 0.46) {
						this.dengji = 4;
						this.dengjides = "还可升级6级";
					} else if (grade == 0.50) {
						this.dengji = 5;
						this.dengjides = "还可升级5级";
					} else if (grade == 0.54) {
						this.dengji = 6;
						this.dengjides = "还可升级4级";
					} else if (grade == 0.58) {
						this.dengji = 7;
						this.dengjides = "还可升级3级";
					} else if (grade == 0.62) {
						this.dengji = 8;
						this.dengjides = "还可升级2级";
					} else if (grade == 0.66) {
						this.dengji = 9;
						this.dengjides = "还可升级1级";
					} else if (grade == 0.7) {
						this.dengji = 10;
						this.dengjides = "已到达最高等级";
					} else {
						this.dengji = 0;
						this.dengjides = "无法识别当前等级";
					}
					this.grade = res.Message.UserGroupId;
					this.isInvitation = res.Message.isInvitation;
					this.$queue.setData("relation", res.Message.invitation);
					this.$queue.setData("gender", parseInt(res.Message.Gender));
					this.gender = parseInt(res.Message.Gender);
					if (res.Message.relationId) {
						this.relation_id = res.Message.relationId;
						this.des = '我的特权';
					} else {
						this.relation_id = '';
						this.des = '会员申请';
					}
					if (res.Message.NickName) {
						this.mobile = res.Message.NickName;
					} else {
						this.mobile = res.Message.Mobile;
					}
				} else {
					this.$queue.logout();
					uni.showModal({
						title: '用户信息提示',
						content: '本地用户信息失效需要重新授权登录',
						showCancel: false,
						success: (e) => {








							this.navTo('/pages/public/login')

						},
					});
				}
			});
		},

		//会员申请弹框
		copyTklWenAn: function() {
			let relation_id = this.$queue.getData("relation_id");
			let token = this.$queue.getData("token");
			let userId = this.$queue.getData("userId");
			if (!token) {
				this.goLoginInfo();
			} else {
				if (!relation_id) {
					this.navTo('/pages/member/publisher?uid=' + userId + '&token=' + token)
				} else {
					uni.showModal({
						showCancel: false,
						title: '我的特权',
						content: '1、享受VIP ' + this.dengji +
							'级返现！\n2、购买商品可享受省钱 +返现！\n3、会员分享商品给好友购买拿返现金额！\n4、更多会员特权我们将陆续上线！\n\n注：VIP越高返现越高哦'
					});
				}
			}
		},
		//获取付款收入查询
		getMoney() {
			let that = this;
			let token = this.$queue.getData("token");
			let relation_id = this.$queue.getData("relation_id");
			if (token && relation_id && relation_id !== 'undefined') {
				//预估本月付款收入查询
				this.$Request.getT("/order/count?relation_id=" + relation_id + "&tk_status=12").then(res => {
					if (res.status === 0 && res.data) {
						that.moneyAll = parseFloat(res.data).toFixed(1);
					} else {
						that.moneyAll = '0';
					}
				});
				//预估本月结算查询
				this.$Request.getT("/order/count?relation_id=" + relation_id + "&tk_status=3").then(res => {
					if (res.status === 0 && res.data) {
						that.jiesuan = parseFloat(res.data).toFixed(1);
					} else {
						that.jiesuan = '0';
					}
				});

				//预估上月结算查询
				this.$Request.getT("/order/last/count?relation_id=" + relation_id + "&tk_status=3").then(res => {
					if (res.status === 0 && res.data) {
						that.lastjiesuan = parseFloat(res.data).toFixed(1);
					} else {
						that.lastjiesuan = '0';
					}
				});
				//可以提现金额查询预估收入查询


				//今日付款订单数
				this.$Request.getT("/order/paidCount/" + relation_id).then(res => {
					if (res.status === 0 && res.data) {
						that.orderNum = res.data;
					} else {
						that.orderNum = '0';
					}
				});


			} else {
				that.totalMoney = '0';
				that.orderNum = '0';
				that.moneys = '0';
				that.moneyAll = '0';
				that.lastjiesuan = '0';
				that.lastMoneyAll = '0';
				that.orderMonthNum = '0';
				that.jiesuan = '0';
			}

		},
		//立即提现
		getMoneys() {
			let token = this.$queue.getData("token");
			if (token) {
				this.navTo('/pages/member/cash');
			} else {
				this.goLoginInfo();
			}

		},
		//结算弹框
		showMessage() {
			uni.showModal({
				showCancel: false,
				title: '结算提示',
				content: '每个月25号结算上月【确认收货】的订单，结算完成后【可提现金额】才会同步更新金额。因结算订单量大，结算时间会较长，结算会在25号晚上完成，建议您26号进行提现。\n举例：5月份确认收货订单，6月25号才会进行结算，以此类推',
			});
		},
		//上月预估收入弹框
		shangyueyugu() {
			uni.showModal({
				showCancel: false,
				title: '上月预估收入说明',
				content: '上月【确认收货】订单的佣金收入\n本月25号结算',
			});
		},
		//积分说明弹框
		jifenDes() {
			uni.showModal({
				showCancel: false,
				title: '积分说明',
				content: '省钱兄中累计付款金额，积分到达一定数量可免费兑换商品',
			});
		},
		//本月结算收入弹框
		benyuejiesuan() {
			uni.showModal({
				showCancel: false,
				title: '本月结算收入说明',
				content: '本月【确认收货】订单的佣金收入\n下月25号结算',
			});
		},
		//本月付款收入说明弹框
		benyuefukuan() {
			uni.showModal({
				showCancel: false,
				title: '本月付款收入说明',
				content: '本月【已付款】订单的佣金收入\n用户付款未确认收货的订单\n部分也包括付款又退单的',
			});
		},
		//今日订单说明弹框
		jinridingdan() {
			showCancel: false,
			uni.showModal({
				title: '今日订单说明',
				content: '今日用户【已付款】的订单笔数',
			});
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			uni.navigateTo({
				url
			})

		},
		//订单详情跳转
		navToOrder(itemid) {
			let relation_id = this.$queue.getData("relation_id");
			if (relation_id && relation_id !== 'undefined') {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid + "&relation_id=" + relation_id,
				})
			} else {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid,
				})
			}
		},

		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navToLogin(url) {
			let token = this.$queue.getData("token");
			let mobile = this.$queue.getData("mobile");
			let userId = this.$queue.getData("userId");
			if (token) {
				this.$Request.getT("/user/" + userId).then(res => {
					if (res.status === 0 && res.data.phone) {
						uni.navigateTo({
							url
						})
					} else {
						uni.navigateTo({
							url: '/pages/public/mobile'
						})
					}
				});
			} else {
				this.goLoginInfo();
			}
		},
		//统一登录跳转
		goLoginInfo() {
			this.$queue.setData("href", '/pages/member/user');






			uni.navigateTo({
				url: '/pages/public/login'
			});

		},
		navToLogins(url) {
			let token = this.$queue.getData("token");
			if (token) {
				let relation_id = this.$queue.getData("relation_id");
				if (relation_id) {
					uni.navigateTo({
						url
					})
				} else {
					uni.showModal({
						showCancel: false,
						title: '温馨提示',
						content: '此功能为会员专享功能\n申请成为会员后可使用',
					});
				}
			} else {
				this.goLoginInfo();
			}

		},

		goLogin(url) {
			let token = this.$queue.getData("token");
			if (!token) {
				this.goLoginInfo();
			}

		},

	}
}
